            <i class="el-icon-success" style="font-size:.8rem;color: #999" @click="choose1" v-if="isShow"></i>
            <i class="el-icon-success" style="font-size:.8rem;color: blue" @click="choose2"  v-if="isShow1"></i>
        </section>
        <div class="footer" @click="queBtn">确定</div>
    </div>

</template>

<script>
    export default {
        name: "Invoice",
        data(){
            return {
                isShow:true,
                isShow1:false
            }
        },
        methods:{
            queBtn(){
                this.$router.push({path:'/confiemorder'})
            },
            choose1(){
                 this.isShow = false;
                 this.isShow1 = true;
            },
            choose2(){
                this.isShow = true;
                this.isShow1 = false;
            }
        }
    }
</script>

